export default {
	data() {
		return {
			style: "",
			secondStyle: "",
			thirdStyle: ""
		}
	},
	methods: {
		selectCombo() {
			this.style = "border:2px solid rgba(249,177,127,1);";
			this.secondStyle = "";
			this.thirdStyle  = "";
		},
		selectCombo2() {
			this.style = "";
			this.secondStyle = "border:2px solid rgba(249,177,127,1);";
			this.thirdStyle  = "";
		},
		selectCombo3() {
			this.style = "";
			this.secondStyle = "";
			this.thirdStyle  = "border:2px solid rgba(249,177,127,1);";
		}
	}
}
